<template>
  <div id="obituary-list">
    <MobileHeader
      title="추모영상"
      class="mb-0"
      right-icon="mdi-help-circle-outline"
      @close="move('/')"
      style="border-bottom : none;"
    />
    <div id="search-area">
      <div class="search-select">
        <v-select
          v-model="searchType"
          :items="searchTypes"
          item-text="name"
          item-value="value"
          outlined
          color="main"
          hide-details
          dense
        ></v-select>
      </div>
      <div class="search-input">
        <v-text-field
          v-model="searchTxt"
          type="text"
          placeholder="검색어를 입력해주세요"
          class="inputStyle"
          outlined
          color="main"
          hide-details
          dense
          @keyup.enter="requestSearch()"
        >
          <template #append>
            <v-icon
              style="background: none"
              color="#988A84"
              @click="requestSearch()"
              >mdi-magnify</v-icon
            >
          </template>
        </v-text-field>
      </div>
    </div>
    <div class="card__wraps mt-6">
      <ObituaryListCard
        v-for="(obituary, index) in getObituaryList"
        :key="index"
        page="movie"
        class="mt-6"
        :obituary="obituary"
        :member-attr="memberInfo.memberAttr"
        @changeCorp="corpChangeFlag = !corpChangeFlag"
      />
    </div>

    <div class="paging">
      <button
        v-if="startPage !== 1"
        class="prev"
        @click="onPageChange(startPage - 1)"
      >
        이전
      </button>
      <a
        v-for="(pageIdx, index) in pages()"
        :key="index"
        :class="{ on: pageIdx === page + 1 }"
        @click="onPageChange(pageIdx)"
        >{{ pageIdx }}</a
      >
      <button
        v-if="getObituaryPageTotal > endPage"
        class="next"
        @click="onPageChange(endPage + 1)"
      >
        다음
      </button>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import myMixin from "@/mixin/mixin";
// import "@/assets/js/jQuery.print.js";
// import "jQuery.print";

import ObituaryListCard from "@/components/obituary/ObituaryListCard.vue";
import MobileHeader from "@/components/common/MobileHeader.vue";

export default {
  name: "Main",
  mixins: [myMixin],
  components: {
    ObituaryListCard,
    MobileHeader
  },
  props: {},

  data() {
    return {
      searchTypes: [
        { name: "고인명", value: "name" },
        { name: "상주명", value: "master" },
        { name: "상주전화번호", value: "phone" }
      ],
      navs: [
        { name: "전체", value: "all" },
        { name: "장례식중", value: "ing" },
        { name: "발인", value: "end" }
      ],
      corpNavs: [
        { name: "전체", value: "all" },
        { name: "장례식중", value: "ing" },
        { name: "발인", value: "end" },
        { name: "비고" },
        { name: "분향소 현황판" }
      ],
      obituary: {
        id: null,
        obituaryName: null,
        obituaryAge: null,
        obituaryAgeYn: null,
        obituarySex: null,
        obituarySexYn: null,
        obituaryMortuary: null,
        obituaryCode: null,
        obituaryDieYn: null,
        obituaryDieDate: null,
        obituaryEnterDate: null,
        obituaryCasketDate: null,
        obituaryCasketYn: null,
        obituaryReligionDate: null,
        obituaryReligionYn: null,
        obituaryEndDate: null,
        obituaryPlace: null,
        obituaryText: null,
        page: {
          page: this.$route.query.page ? this.$route.query.page : 0,
          size: this.$route.query.size ? this.$route.query.size : 8,
          sort: this.$route.query.sort ? this.$route.query.sort : "regDate,desc"
        }
      },
      changeType: this.$route.query.changeType
        ? this.$route.query.changeType
        : "",
      endFlag: this.$route.query.endFlag ? this.$route.query.endFlag : "ing",
      searchTxt: this.$route.query.searchTxt ? this.$route.query.searchTxt : "",
      searchType: this.$route.query.searchType
        ? this.$route.query.searchType
        : "name",
      pages: function() {
        const list = [];
        for (let i = this.startPage; i <= this.endPage; i += 1) list.push(i);
        return list;
      },

      page: 0
    };
  },
  computed: {
    ...mapGetters([
      "getMainNoticeList",
      "getObituaryList",
      "getObituaryPage",
      "getObituaryPageTotal",
      "getObituaryTotalElements",
      "getObituaryCount",
      "getObituaryReadCount",
      "memberInfo"
    ]),
    startPage() {
      return parseInt((this.getObituaryPage - 1) / 5) * 5 + 1;
    },
    endPage() {
      return (parseInt((this.getObituaryPage - 1) / 5) + 1) * 5 <
        this.getObituaryPageTotal
        ? (parseInt((this.getObituaryPage - 1) / 5) + 1) * 5
        : this.getObituaryPageTotal;
    }
  },
  created() {
    this.$store.dispatch("REQUEST_MAIN");
    this.requestGetObituaryList();
  },
  mounted() {
    //let recaptchaScript = document.createElement('script')
    //recaptchaScript.setAttribute('src', 'https://www.google.com/recaptcha/api.js')
    //document.head.appendChild(recaptchaScript)
  },
  beforeDestroy() {
    this.$store.commit("OBITUARY_INIT");
  },
  methods: {
    routingBack() {
      this.$router.go(-1);
    },

    move(path) {
      this.$router.push(path);
    },

    onPageChange(val) {
      const param = {
        page: val
      };
      this.requestObituaryList(param);
    },
    requestObituaryList(param) {
      let currentPage = 0;
      if (
        param !== undefined &&
        param !== null &&
        param.page !== undefined &&
        param.page !== null
      ) {
        currentPage = param.page - 1;
      }
      this.$router.push({
        path: "/movie",
        query: {
          changeType: this.changeType,
          endFlag: this.endFlag,
          searchTxt: this.searchTxt,
          searchType: this.searchType,
          page: currentPage,
          size: this.obituary.page.size,
          sort: this.obituary.page.sort
        }
      });
    },
    requestGetObituaryList() {
      const parameter = {
        obituary: this.obituary,
        endFlag: this.endFlag,
        searchTxt: this.searchTxt,
        searchType: this.searchType
      };
      this.$store.dispatch("REQUEST_OBITUARY_LIST", parameter);
    },
    requestSearch(flag) {
      this.changeType = "";
      if (flag !== null && flag !== undefined) {
        this.endFlag = flag;
      }
      this.requestObituaryList();
    }
  }
};
</script>

<style type="text/css" media="print">
@page {
  size: auto; /* auto is the initial value */
  margin: 0; /* this affects the margin in the printer settings */
}
</style>
